import { ScrollMagicArrowsSvg } from '../ScrollMagicArrowsSvg';

//   svg
const trigger = '#results-chart__trigger';
const pathLine = 'path#results-chart-path-line';
const pathArrow = 'path#results-chart-path-arrow';
const iconArrow = '.results-chart__icon-arrow';

ScrollMagicArrowsSvg({ pathLine, pathArrow, iconArrow, trigger });

//     .
let screenWidth = window.innerWidth;
let restart = false;

if (screenWidth >= 1170) {
  restart = true;
}

window.matchMedia('(min-width: 1170px)').addEventListener('change', e => {
  if (!e.matches) return;
  if (restart) return;
  restart = true;

  ScrollMagicArrowsSvg({ pathLine, pathArrow, iconArrow, trigger });
});
