import * as ScrollMagic from 'scrollmagic';
import { TweenMax, TimelineMax, Linear } from 'gsap';
import $ from 'jquery';
import {
  ScrollMagicPluginIndicator,
  ScrollMagicPluginGsap,
  ScrollMagicPluginJQuery,
} from 'scrollmagic-plugins';

ScrollMagicPluginIndicator(ScrollMagic);
ScrollMagicPluginGsap(ScrollMagic, TweenMax, TimelineMax);
ScrollMagicPluginJQuery(ScrollMagic, $);

let screenWidth = window.innerWidth;
let restart = false;

let controller = new ScrollMagic.Controller();

function ScrollMagicArrowsSvg({ pathLine, pathArrow, iconArrow, trigger }) {
  if (screenWidth <= 1169) return;

  restart = true;

  function pathPrepare({ $pathLine }) {
    let pathLineLength = $pathLine[0].getTotalLength();

    $pathLine.css('stroke-dasharray', pathLineLength);
    $pathLine.css('stroke-dashoffset', pathLineLength);

    return pathLineLength;
  }

  let $pathLine = $(pathLine);
  let $pathArrow = $(pathArrow);
  let $iconArrow = $(iconArrow);

  let LineLength = pathPrepare({ $pathLine });

  //tween
  let tween = new TimelineMax()
    .add(
      TweenMax.to($pathLine, 1, { strokeDashoffset: 0, ease: Linear.easeNone }),
      0
    )
    .add(
      TweenMax.to($pathArrow, 1, { stroke: '#FAFF00', ease: Linear.easeNone }),
      0
    )
    .add(
      TweenMax.to($iconArrow, 1, {
        translateY: LineLength,
        ease: Linear.easeNone,
      }),
      0
    );

  //scene
  let scene = new ScrollMagic.Scene({
    triggerElement: trigger,
    duration: LineLength,
    offset: 50,
    tweenChanges: true,
  })
    .setTween(tween)
    .addIndicators() // add indicators (requires plugin)
    .addTo(controller);
}

//     .

window.matchMedia('(min-width: 1170px)').addEventListener('change', e => {
  if (!e.matches) return;
  if (restart) return;
  restart = true;

  console.log('min-width: 1170px');
  screenWidth = window.innerWidth;
});

export { ScrollMagicArrowsSvg };
