function createLazyImage({
    imageDesktopBase,
    imageMobileBase,
    imageDesktopX2,
    imageMobileX2,
    imageDesktopX3,
    imageMobileX3,
    imageWrapper
}) {
    let screenWidth = window.innerWidth;

    const uploadedImage = () => {
        if (screenWidth <= 1169) {
            return imageMobileX2;
        }
        if (screenWidth >= 1170) {
            return imageDesktopX2;
        }
    }

    const baseImage = () => {
        if (screenWidth <= 1169) {
            return imageMobileBase;
        }
        if (screenWidth >= 1170) {
            return imageDesktopBase;
        }
    };

    function createImageMarcup() {
        const listCards = `
            <picture>
                <source media="(min-width: 1170px)" data-srcset="${imageDesktopX2} 1x, ${imageDesktopX2} 2x, ${imageDesktopX3} 3x"/>
                <source media="(max-width: 1169px)" data-srcset="${imageMobileX2} 1x, ${imageMobileX2} 2x, ${imageMobileX3} 3x"/>
                <img alt="image-bull" class="lazy" src="${baseImage()}" data-src="${uploadedImage()}"/>
            </picture>
        `

        return listCards
    }

    imageWrapper.innerHTML = createImageMarcup();
}

export { createLazyImage };
